:root {
  --font-family: "NunitoSans", "Nextartbold", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --light-grey: #a0a0a0;
  --bright-yellow: #edff21;
  --dark-green: #3f727e;
  --transparent-black: rgb(34, 41, 45);
}

