html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background-color: #2a2a29;
  background-image: url("../img/logo-2.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 1000px 500px;
}
@media (max-width: 1024px) {
  .page__body {
    background-size: 800px 300px;
  }
}
@media (max-width: 768px) {
  .page__body {
    background-size: 600px 150px;
  }
}
@media (max-width: 576px) {
  .page__body {
    background-size: 300px 150px;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.logo {
  display: flex;
  flex-shrink: 0;
  width: 170px;
}
.logo svg {
  fill: var(--light-color);
}
@media (max-width: 576px) {
  .logo {
    width: 150px;
    height: 100px;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 1024px) {
  .nav__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    margin-bottom: 25px;
  }
}
.nav__item:not(:last-child) {
  margin-right: 25px;
}
@media (max-width: 1024px) {
  .nav__item {
    display: flex;
    margin-right: 25px;
  }
}
.nav__link {
  font-size: 18px;
  color: var(--light-color);
}
@media (max-width: 1024px) {
  .nav__link {
    font-size: 22px;
  }
}

.water-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  width: 280px;
  height: 60px;
  border-radius: 0 0 10px 10px;
  outline: none;
  transition: all 0.8s ease 0s;
}
.water-btn:focus {
  outline: 1px solid var(--bright-yellow);
  outline-offset: 3px;
}
.water-btn:hover {
  border-radius: 10px;
  box-shadow: 0 0 40px rgba(73, 115, 255, 0.6);
  transition: all 0.8s ease 0.2s;
}
.water-btn:hover .water-btn__waves {
  top: -50px;
}
.water-btn__text {
  position: relative;
  z-index: 1;
  font-family: "Ubuntu";
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--light-color);
  transition: all 0.8s ease 0s;
}
.water-btn__waves {
  position: absolute;
  top: 0;
  left: 0;
  width: 280px;
  height: 280px;
  background-color: #4973ff;
  box-shadow: 0 0 50px var(--translucent-black);
  transition: all 0.8s ease 0s;
}
.water-btn__waves::after, .water-btn__waves::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 250%;
  height: 250%;
  transform: translate3d(-50%, -96%, 0) rotate(0deg) scale(1);
}
.water-btn__waves::before {
  border-radius: 48%;
  background-color: var(--light-blue-green);
  animation: waves 5s infinite linear;
}
.water-btn__waves::after {
  border-radius: 44%;
  background-color: rgba(0, 182, 196, 0.5);
  animation: waves 10s infinite linear;
}
@media (max-width: 1024px) {
  .water-btn {
    display: none;
  }
}
.water-btn--burger {
  display: flex;
}

@keyframes waves {
  0% {
    transform: translate3d(-50%, -96%, 0) rotate(0deg) scale(1);
  }
  100% {
    transform: translate3d(-50%, -96%, 0) rotate(360deg) scale(1);
  }
}
.header {
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  margin-right: 25px;
}
@media (max-width: 1024px) {
  .header__nav-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    overflow-y: auto;
    width: 80%;
    height: 100vh;
    box-shadow: 10px 4px 15px 0px rgba(34, 60, 80, 0.25);
    padding: 51px 100px;
    margin-right: 0;
    background-color: var(--dark-green);
    opacity: 0;
    visibility: hidden;
    transform: translateX(-150%);
    transition: opacity 0.3s, visibility 0.3s, transform 0.3s;
  }
}
@media (max-width: 576px) {
  .header__nav-list {
    width: 100%;
    padding: 30px 20px 20px;
  }
}
.header__wrap-contacts {
  display: flex;
  width: 60%;
}
.header__wrap-consultation--burger {
  display: none;
}
@media (max-width: 1024px) {
  .header__wrap-consultation--burger {
    display: block;
  }
}

.burger {
  --burger-width: 30px;
  --burger-height: 30px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  --burger-width: 35px;
  --burger-height: 25px;
  position: absolute;
  top: 43%;
  right: 3%;
  z-index: 1000;
  display: none;
  color: var(--light-color);
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media (max-width: 1024px) {
  .burger {
    display: block;
  }
}
@media (max-width: 576px) {
  .burger {
    z-index: 1001;
    top: 33%;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
  transform: none;
}